import axios from 'axios'
import dayjs from 'dayjs'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { Navigate, useParams } from 'react-router'

import LocalPoliceIcon from '@mui/icons-material/LocalPolice'
import ManageSearchIcon from '@mui/icons-material/ManageSearch'
import ShieldIcon from '@mui/icons-material/Shield'
import VerifiedIcon from '@mui/icons-material/Verified'
import { Box, Chip, type Theme, Tooltip, Typography, colors, useMediaQuery } from '@mui/material'
import type {
  GridColDef,
  GridRenderCellParams,
  GridRowParams,
  GridRowSelectionModel,
  GridValueFormatterParams
} from '@mui/x-data-grid'
import { DataGrid, GRID_CHECKBOX_SELECTION_COL_DEF, GridActionsCellItem } from '@mui/x-data-grid'

import { MEMBER_VALUES } from 'src/data/MemberValues'
import { CLUB_MEMBER_MESSAGE } from 'src/data/messages'

import { getClubMember, putClubMember } from 'src/api/clubManage'

import { type ClubMember } from 'src/models/ClubMember'
import { type DateProps } from 'src/models/date'

import CustomGridToolbar from 'src/content/manage/Member/MemberGrid/CustomGridToolbar'

import { useGetFeeAmounts } from 'src/hooks/api/accountHooks'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'

import DetailsModal from './DetailsModal'
import 'moment/locale/ko'

const getStatusValue = (params: GridRenderCellParams<ClubMember>) => {
  if (params.row.withdrawalAt) {
    return MEMBER_VALUES.status[3].value
  }
  if (params.row.firedAt) {
    return MEMBER_VALUES.status[2].value
  }
  if (params.value) {
    return MEMBER_VALUES.status[1].value
  }

  return MEMBER_VALUES.status[0].value
}

const MemberGrid: React.FC<DateProps> = date => {
  const { id } = useParams()
  const clubId = Number(id)

  const matches = useMediaQuery((theme: Theme) => theme.breakpoints.up('sm'))
  const { enqueueSnackbar } = useSnackbar()

  const { userInfo } = useSafeAuthUser()
  const authHeader = useAuthHeader()

  const [members, setMembers] = useState<ClubMember[]>([])
  const [modalMembers, setModalMembers] = useState<ClubMember[]>([])
  const [selectedMemberIds, setSelectedMemberIds] = useState<GridRowSelectionModel>([])

  const club = userInfo?.clubs.find(({ id }) => id === clubId)
  const selectedMembers = members.filter(({ id }) => selectedMemberIds.includes(id))
  const isAllMembersNotBelongToClub = selectedMembers.every(({ withdrawalAt, firedAt }) => !!withdrawalAt || !!firedAt)

  useEffect(() => {
    axios
      .get<ClubMember[]>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/member`, {
        headers: { Authorization: authHeader() },
        params: date
      })
      .then(({ data }) => setMembers(data))
      .catch(error =>
        enqueueSnackbar(error?.response?.data?.message ?? CLUB_MEMBER_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
      )
  }, [date, modalMembers])

  const handleRowSelectionChange = (selectedMemberIds: GridRowSelectionModel) => {
    setSelectedMemberIds(selectedMemberIds)
  }

  const { data: feeAmounts, isSuccess } = useGetFeeAmounts(clubId, date)

  const existingMemberLevel = isSuccess ? feeAmounts.map(({ level }) => level) : []

  const handleUpdatedMembers = (updatedMembers: ClubMember[]) => {
    axios
      .patch(
        `${process.env.REACT_APP_APIHOST}/club/${clubId}/member`,
        updatedMembers.map(member => ({ ...member, clubId, userId: member.id })),
        {
          params: date,
          headers: { Authorization: authHeader() }
        }
      )
      .then(() => {
        const updatedIndices = updatedMembers.map(member => member.id)
        setMembers(prev =>
          prev.map(member =>
            updatedIndices.includes(member.id) ? updatedMembers[updatedIndices.indexOf(member.id)] : member
          )
        )
        setModalMembers([])

        enqueueSnackbar(CLUB_MEMBER_MESSAGE.PATCH_SUCCESS, {
          variant: 'success'
        })
      })
      .catch(error => {
        if (error?.response?.status === 404) {
          enqueueSnackbar(CLUB_MEMBER_MESSAGE.NOT_LEVEL_EXIST, {
            variant: 'error'
          })

          return
        }

        enqueueSnackbar(error?.response?.data?.message ?? CLUB_MEMBER_MESSAGE.SERVER_ERROR, {
          variant: 'error'
        })
      })
  }

  const handleSearchButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    setModalMembers(selectedMembers)
  }

  const handleFireButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    const updatedMembers = selectedMembers.map(member => ({ ...member, firedAt: dayjs(new Date()).toISOString() }))
    handleUpdatedMembers(updatedMembers)
  }
  useEffect(() => {
    axios
      .get<ClubMember[]>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/member`, {
        headers: { Authorization: authHeader() },
        params: date
      })
      .then(({ data }) => setMembers(data))
      .catch(error =>
        enqueueSnackbar(error?.response?.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }, [date])

  const handleDeleteButtonClick: React.MouseEventHandler<HTMLButtonElement> = async () => {
    try {
      const members = selectedMembers.map(member => ({ userId: member.id, semesterInfo: date }))
      await putClubMember({ clubId, members, token: authHeader() })

      const updatedMembers = await getClubMember({ clubId, token: authHeader(), date })
      setMembers(updatedMembers)
      enqueueSnackbar(CLUB_MEMBER_MESSAGE.PUT_SUCCESS, { variant: 'success' })
    } catch {
      enqueueSnackbar(CLUB_MEMBER_MESSAGE.DELETE_FAIL, { variant: 'error' })
    }
  }

  const handleExportButtonClick: React.MouseEventHandler<HTMLButtonElement> = () => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/migration/${clubId}`, {
        headers: { Authorization: authHeader() },
        responseType: 'blob'
      })
      .then(({ data }) => {
        const blob = new Blob([data])
        const fileObjectUrl = window.URL.createObjectURL(blob)

        const link = document.createElement('a')
        link.href = fileObjectUrl
        link.style.display = 'none'

        link.download = `${club?.name}.xlsx`

        document.body.appendChild(link)
        link.click()
        link.remove()

        window.URL.revokeObjectURL(fileObjectUrl)
      })
      .catch(error => {
        enqueueSnackbar(error?.response?.data?.message ?? CLUB_MEMBER_MESSAGE.SERVER_ERROR, { variant: 'error' })
      })
  }

  const columns: GridColDef[] = [
    {
      ...GRID_CHECKBOX_SELECTION_COL_DEF,
      headerName: '선택'
    },
    {
      field: 'actions',
      type: 'actions',
      headerName: '상세',
      width: 50,
      getActions: (params: GridRowParams<ClubMember>) => [
        <Tooltip title="클릭하면 회원 정보를 더 자세하게 확인할 수 있습니다.">
          <GridActionsCellItem
            icon={<ManageSearchIcon />}
            label="자세히 보기"
            onClick={() => setModalMembers([params.row])}
          />
        </Tooltip>
      ]
    },
    {
      field: 'name',
      headerName: '이름',
      width: 140,
      renderCell: (params: GridRenderCellParams<ClubMember>) => (
        <>
          <Typography sx={{ maxWidth: '80px', overflow: 'hidden', textOverflow: 'ellipsis' }}>
            {params.value}
          </Typography>
          {params.row.isAuthenticated && (
            <Tooltip title="KLUB에서 인증 완료된 회원입니다.">
              <VerifiedIcon color="primary" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
          {params.row.permission == 'SUPERADMIN' && (
            <Tooltip title="최고 운영진 권한을 가진 회원입니다. KLUB에서 제공하는 운영진 기능을 모두 이용할 수 있습니다.">
              <LocalPoliceIcon color="warning" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
          {params.row.permission == 'ADMIN' && (
            <Tooltip title="일반 운영진 권한을 가진 회원입니다. 권한 변경을 제외한, KLUB에서 제공하는 운영진 기능을 모두 이용할 수 있습니다.">
              <ShieldIcon color="warning" fontSize="small" sx={{ marginLeft: '2px' }} />
            </Tooltip>
          )}
        </>
      )
    },
    { field: 'studentId', headerName: '학번', width: 110 },
    { field: 'email', headerName: '이메일', width: 180 },
    { field: 'phoneNumber', headerName: '전화번호', width: 130 },
    { field: 'department', headerName: '소속 학부/학과', width: 180 },
    { field: 'level', headerName: '회원 등급', width: 100 },
    {
      field: 'isPaused',
      headerName: '활동 상태',
      width: 100,
      valueGetter: (params: GridRenderCellParams<ClubMember>) => getStatusValue(params),
      renderCell: (params: GridRenderCellParams<ClubMember>) => {
        let display: string = MEMBER_VALUES.status[3].display
        let statusColor: string = colors.grey[800]
        let bgColor: string = colors.grey[50]

        switch (params.value) {
          case MEMBER_VALUES.status[3].value:
            display = MEMBER_VALUES.status[3].display
            statusColor = colors.grey[800]
            bgColor = colors.grey[50]
            break
          case MEMBER_VALUES.status[2].value:
            display = MEMBER_VALUES.status[2].display
            statusColor = colors.red[800]
            bgColor = colors.red[50]
            break
          case MEMBER_VALUES.status[1].value:
            display = MEMBER_VALUES.status[1].display
            statusColor = colors.blue[800]
            bgColor = colors.blue[50]
            break
          default:
            display = MEMBER_VALUES.status[0].display
            statusColor = colors.green[800]
            bgColor = colors.green[50]
            break
        }
        return <Chip label={display} size="small" sx={{ color: statusColor, bgcolor: bgColor }} />
      }
    },
    {
      field: 'attendance',
      headerName: '출석 횟수',
      width: 100,
      renderCell: (params: GridRenderCellParams<ClubMember>) => `${params.value} / ${params.row.totalAttendance}`
    },
    {
      field: 'fee',
      headerName: '회비 납부',
      width: 90,
      valueFormatter: (params: GridValueFormatterParams<boolean>) => (params.value ? '납부함' : '미납')
    },
    { field: 'note', headerName: '비고', width: 280 }
  ]

  if (!club) {
    enqueueSnackbar('잘못된 접근입니다.', { variant: 'error' })
    return <Navigate to="/" />
  }

  return (
    <>
      <Box style={{ height: matches ? 720 : 480, width: '100%' }}>
        <DataGrid
          rows={members}
          columns={columns}
          rowSelectionModel={selectedMemberIds}
          onRowSelectionModelChange={handleRowSelectionChange}
          slots={{
            toolbar: () => (
              <CustomGridToolbar
                isAllSelectedMembersNotBelongToClub={isAllMembersNotBelongToClub}
                selectedMemberIds={selectedMemberIds}
                handleDeleteButtonClick={handleDeleteButtonClick}
                handleExportButtonClick={handleExportButtonClick}
                handleFireButtonClick={handleFireButtonClick}
                handleSearchButtonClick={handleSearchButtonClick}
              />
            )
          }}
          initialState={{
            columns: {
              columnVisibilityModel: {
                email: false,
                phoneNumber: false,
                studentId: window.innerWidth >= 600,
                level: window.innerWidth >= 600,
                isPaused: window.innerWidth >= 600,
                attendance: window.innerWidth >= 600,
                fee: window.innerWidth >= 600,
                note: window.innerWidth >= 600
              }
            }
          }}
          sx={{ bgcolor: 'white', borderRadius: matches ? '10px' : 0 }}
          autoPageSize
          checkboxSelection
          disableRowSelectionOnClick
        />
      </Box>
      <DetailsModal
        userPermission={club.permission}
        targetMembers={modalMembers}
        setTargetMembers={setModalMembers}
        handleUpdate={handleUpdatedMembers}
        existingMemberLevels={existingMemberLevel}
        clubId={clubId}
      />
    </>
  )
}

export default MemberGrid
